$color-fondo: #ffffff;
$color-letra: rgb(0, 0, 0);
$color-primario: #225bc5;
$color-secundario: #b5cfff;
$color-blanco: #fff;
$color-logo: #5d5d5d;


/*------------------------------------
- COLOR portfolio
------------------------------------*/

$portfolio_t1: #225bc5;
$portfolio_t2: #1a4699;
$portfolio_t3: #97b5ed;
$portfolio_t4: #adc5f0;
$portfolio_t5: #0f2a5c;
$portfolio_t6: #18408c;
$portfolio_t7: #1c4ca6;
$portfolio_t8: #081631;
$portfolio_t9: #bfd1f3;

$portfolio_shadow: rgba(34, 91, 197, 0.5);
$portfolio_text_color_dark_bright: #fff;


.alert-portfolio {
  color: $portfolio_t5;
  background-color: $portfolio_t9;
  border-color: $portfolio_t4;
}

.alert-portfolio hr {
  border-top-color: $portfolio_t3;
}

.alert-portfolio .alert-link {
  color: $portfolio_t8;
}

.badge-portfolio {
  color: $portfolio_text_color_dark_bright;
  background-color: $portfolio_t1;
}

.badge-portfolio[href]:hover, .badge-portfolio[href]:focus {
  color: $portfolio_text_color_dark_bright;
  background-color: $portfolio_t2;
}

.bg-portfolio {
  background-color: $portfolio_t1 !important;
}

a.bg-portfolio:hover, a.bg-portfolio:focus,
button.bg-portfolio:hover,
button.bg-portfolio:focus {
  background-color: $portfolio_t2 !important;
}

.border-portfolio {
  border-color: $portfolio_t1 !important;
}

.btn-portfolio {
  color: $portfolio_text_color_dark_bright;
  background-color: $portfolio_t1;
  border-color: $portfolio_t1;
}

.btn-portfolio:hover {
  color: $portfolio_text_color_dark_bright;
  background-color: $portfolio_t7;
  border-color: $portfolio_t2;
}

.btn-portfolio:focus, .btn-portfolio.focus {
  box-shadow: 0 0 0 0.2rem $portfolio_shadow;
}

.btn-portfolio.disabled, .btn-portfolio:disabled {
  color: $portfolio_text_color_dark_bright;
  background-color: $portfolio_t1;
  border-color: $portfolio_t1;
}

.btn-portfolio:not(:disabled):not(.disabled):active, .btn-portfolio:not(:disabled):not(.disabled).active, .show > .btn-portfolio.dropdown-toggle {
  color: $portfolio_text_color_dark_bright;
  background-color: $portfolio_t2;
  border-color: $portfolio_t6;
}

.btn-portfolio:not(:disabled):not(.disabled):active:focus, .btn-portfolio:not(:disabled):not(.disabled).active:focus, .show > .btn-portfolio.dropdown-toggle:focus {
  box-shadow: 0 0 0 0.2rem $portfolio_shadow;
}

.btn-outline-portfolio {
  color: $portfolio_t1;
  background-color: transparent;
  border-color: $portfolio_t1;
}

.btn-outline-portfolio:hover {
  color: $portfolio_text_color_dark_bright;
  background-color: $portfolio_t1;
  border-color: $portfolio_t1;
}

.btn-outline-portfolio:focus, .btn-outline-portfolio.focus {
  box-shadow: 0 0 0 0.2rem $portfolio_shadow;
}

.btn-outline-portfolio.disabled, .btn-outline-portfolio:disabled {
  color: $portfolio_t1;
  background-color: transparent;
}

.btn-outline-portfolio:not(:disabled):not(.disabled):active, .btn-outline-portfolio:not(:disabled):not(.disabled).active, .show > .btn-outline-portfolio.dropdown-toggle {
  color: $portfolio_text_color_dark_bright;
  background-color: $portfolio_t1;
  border-color: $portfolio_t1;
}

.btn-outline-portfolio:not(:disabled):not(.disabled):active:focus, .btn-outline-portfolio:not(:disabled):not(.disabled).active:focus, .show > .btn-outline-portfolio.dropdown-toggle:focus {
  box-shadow: 0 0 0 0.2rem $portfolio_shadow;
}

.list-group-item-portfolio {
  color: $portfolio_t5;
  background-color: $portfolio_t4;
}

.list-group-item-portfolio.list-group-item-action:hover, .list-group-item-portfolio.list-group-item-action:focus {
  color: $portfolio_t5;
  background-color: $portfolio_t3;
}

.list-group-item-portfolio.list-group-item-action.active {
  color: $portfolio_text_color_dark_bright;
  background-color: $portfolio_t5;
  border-color: $portfolio_t5;
}

.table-portfolio,
.table-portfolio > th,
.table-portfolio > td {
  background-color: $portfolio_t4;
}

.table-hover .table-portfolio:hover {
  background-color: $portfolio_t3;
}

.table-hover .table-portfolio:hover > td,
.table-hover .table-portfolio:hover > th {
  background-color: $portfolio_t3;
}

.text-portfolio {
  color: $portfolio_t1 !important;
}

a.text-portfolio:hover, a.text-portfolio:focus {
  color: $portfolio_t2 !important;
}
