@import 'variables.scss';

@import '~ngx-sharebuttons/themes/default/default-theme';
@import '~ngx-sharebuttons/themes/circles/circles-dark-theme';
@import '~ngx-sharebuttons/themes/modern/modern-dark-theme';
@import '~ngx-sharebuttons/themes/material/material-dark-theme';

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: Montserrat, system-ui, sans-serif;
}

html {
    color: $color-letra;
}

::-webkit-scrollbar {
    background: $color-blanco;
    width: 5px;
}

::-webkit-scrollbar-track {
    background: $color-blanco;
}

::-webkit-scrollbar-thumb {
    background: $color-primario;
    border-radius: 10px;
}

.owl-item, .owl-stage, .owl-stage-outer, .owl-carousel {
    height: 100% !important;
}

// Progress Bar
.progressbar{
    height:5px;
    width:100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 9999;
    background-color: $color-blanco
  }
  .progressbar__highlight{
    transition: width 0.15s linear;
    background: $color-primario;
    height:100%;
    width:0%;
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
  }